import React from 'react';
import './order.scss';
import request from './request';

import data  from '../data';
const { orderData } = data;

export default class Order extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            orderInfo: {
                order_date: '2018.6.7',
                order_path: '旅游1线',
                order_station: '南门换乘车站',
                order_class: '9:00',
                order_ticket_count: 2,
                order_owner: '曾超',
                order_mobile: '13636672480',
            },
            dateElement: null,
        }
    }

    renderSuccess = () => {
        const {
            order_date,
            order_path,
            order_station,
            order_class,
            order_ticket_count,
            order_owner,
            order_mobile

        } = this.state.orderInfo;

        return (
            <div className="order-success-page">
                <div className="log"/>
                <div className="success-title">提交成功</div>
                <div className="content">
                    <div className="title">预订信息</div>
                    <div>选择日期：{order_date}</div>
                    <div>选择线路：{order_path}</div>
                    <div>选择站点：{order_station}</div>
                    <div>选择班次：{order_class}</div>
                    <div>选择张数：{order_ticket_count}</div>
                    <div>预订人：{order_owner}</div>
                    <div>手机号：{order_mobile}</div>
                </div>
            </div>
        )
    }

    submit = () => {
        request.post('/cbo/addOrder', this.state.orderInfo).then(response => {

        }).catch(error => {

        });
    }

    selectPath = (order_path) => {
        const orderInfo = {...this.state.orderInfo, order_path}
        this.setState({ orderInfo });
    }

    selectStation = (order_station) => {
        const orderInfo = {...this.state.orderInfo, order_station}
        this.setState({ orderInfo });
    }

    renderStationsSelect = () => {
        const selectPath = orderData.filter(path => path.name === this.state.orderInfo.order_path );
        if (selectPath && selectPath.length > 0) {
            return selectPath[0].stations.map(station => {
                return <option key={station.name} onClick={() => this.selectStation(station.name)} value={station.name}>{station.name}</option>
            })
        }
        return null
    }

    renderOrder = () => {
        const {
            order_date,
            order_path,
            order_station,
            order_class,
            order_ticket_count,
            order_owner,
            order_mobile

        } = this.state.orderInfo;

        return (
            <div className="order-page">
                <div>
                    <span>选择日期</span>
                    <input type='date' value={order_date} />
                </div>
                <div>
                    <span>选择路线</span>
                    <select value={order_path} onChange={(e) => {
                        this.setState({ orderInfo: {...this.state.orderInfo, order_path: event.target.value }});
                    }}>
                        {
                            orderData.map(path => {
                                return (
                                    <option key={path.name} onClick={() => {
                                        this.selectPath(path.name)
                                    }} value={path.name}>{path.name}</option>
                                )
                            })
                        }
                    </select>
                </div>
                <div>
                    <span>选择站点</span>
                    <select>
                        {
                            this.renderStationsSelect()
                        }
                    </select>
                </div>
                <div>
                    <span>选择班次</span>
                    <select>
                        <option value="volvo">Volvo</option>
                        <option value="saab">Saab</option>
                        <option value="opel">Opel</option>
                        <option value="audi">Audi</option>
                    </select>
                </div>
                <div>
                    <span>座位预订</span>
                    <select>
                        <option value="volvo">Volvo</option>
                        <option value="saab">Saab</option>
                        <option value="opel">Opel</option>
                        <option value="audi">Audi</option>
                    </select>
                </div>
                <div>
                    <div>请仔细核对预约信息</div>
                    <button onClick={this.submit}>提交</button>
                </div>
            </div>
        )
    }

    render () {
        // return this.renderSuccess();
        return this.renderOrder();
    }
}


